@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #17232a;
  color: #ffffff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* font-family: 'Fira Sans', sans-serif; */

/* Customize your main colors in :root variables */
:root {
  --main-background-color: #000000;
  --card-background-color: rgba(0, 0, 0, 0.5);
  --countdown-background-color: #433765;
  --main-text-color: #d94242;
  --title-text-color: #ffffff;
  --main-color-bg: #17232a;
}

body {
  background-color: #17232a;
  background-color: var(--main-color-bg);
  background: url(/static/media/cubes2.4ae9d412.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
  font-family: "VT323", monospace;
  font-size: 1.1em;
  background-color: #000000;
}

h2,
p {
  color: #d94242;
  color: var(--main-text-color);
}

h3 {
  font-weight: normal;
}

.MsgSpan {
  color: var(--title-text-color);
  text-shadow: 0 0 10px black;
  font-size: 30px;
}

